import React from 'react'
import Tab from './Tab'
import { Link } from 'react-router-dom';
const Detail2 = () => {
    return (
        <div>
            <img src={'https://gw.alicdn.com/bao/uploaded/i2/1742672956/TB2UHOQoXXXXXb3XpXXXXXXXXXX_!!1742672956.jpg_500x500q90.jpg_.webp'} style={{width:'100%',position:'absolute'}} />
            {/* absolute是相对于浏览器定位 */}
            <Link to='/react3/build'>
                <button
                    style={{
                        width:'30px',
                        height:'30px',
                        background:'rgba(0, 0, 0, 0.4)',
                        color:'#fff',
                        fontSize:'20px',
                        borderRadius:'100%',
                        position:'relative',    //relative是相对于前面容器定位
                        margin:'15px'
                    }}
                ><span>&lt;</span></button>
            </Link>
            <Tab />
        </div> 
    )
}
export default Detail2;