import React from 'react'
// import { WhiteSpace } from 'antd-mobile';
// import moduleName from '../assets/1.jpg'
import Search from './Search'
import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';

import CarouselLower from './CarouselLower'
import List from './List'
import Mytab from './Mytab'
class Home  extends React.Component {
    render(){
        return (
            <div>
                {/* 搜索框 */}
                <SearchBar
                    placeholder="请输入你要搜索的内容"
                    // 点击跳转搜索页面
                    onFocus={() => this.props.history.push('/home/search')}
                    // onFocus={() => {window.open("/home/search")}}
                />
                {/* 轮播图 */}
                <CarouselLower></CarouselLower>
                {/* 商品列表 */}
                <WhiteSpace />
    
                <List></List>
                <WhiteSpace />
    
                <List></List>
                <WhiteSpace />
    
                <List></List>
                <WhiteSpace />
    
                <List></List>
                <WhiteSpace />
                
                <List></List>
                <WhiteSpace />
                {/* <img style={{width:'100%'}} src={require('../assets/1.jpg')} alt="" /> */}
                {/* <img src={img} alt=""/> */}
                {/* <img src="https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg" alt=""/> */}
                {/* <img style={{fontSize:100,color:'red'}} className='iconfont icon-dingdan' /> */}
                {/* <i onClick={() => }className='iconfont icon-wode'></i>      */}
                <Mytab />
            </div>
        )
    }
    
}
export default Home;